.sub {
  position: relative;
  line-height: 56px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 640px;
}
.card-bg3 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: var(--br-base);
  background-color: var(--white-60);
  box-shadow: var(--translucency-m);
  backdrop-filter: blur(32px);
  border: 1px solid var(--white-25);
  box-sizing: border-box;
  overflow: hidden;
}
.cover-img-icon1 {
  position: relative;
  border-radius: var(--br-5xs);
  width: 320px;
  height: 520px;
  object-fit: cover;
}
.text4,
.title4 {
  align-self: stretch;
  position: relative;
  line-height: 24px;
}
.title4 {
  font-weight: 500;
}
.text4 {
  font-size: var(--desktop-p-2-aa-medium-size);
  color: var(--shades-2);
}
.description1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-5xs);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.link7,
.share1 {
  position: relative;
  font-weight: 500;
}
.share1 {
  line-height: 24px;
  display: none;
}
.link7 {
  text-decoration: underline;
  line-height: 20px;
}
.icon-24px-actions-copy-fat1 {
  position: relative;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.action2 {
  border-radius: var(--br-9xs);
  background-color: var(--shades-white);
  display: flex;
  flex-direction: row;
  padding: var(--padding-9xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.about1,
.actions2,
.actions3 {
  display: flex;
  align-items: center;
}
.actions3 {
  flex-direction: row;
  justify-content: flex-start;
  gap: var(--gap-xs);
  color: var(--solid-5);
}
.about1,
.actions2 {
  gap: var(--gap-base);
}
.actions2 {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  text-align: left;
  font-size: var(--desktop-p-2-aa-medium-size);
}
.about1 {
  width: 320px;
  flex-direction: column;
  justify-content: flex-start;
}
.qr-icon1 {
  position: relative;
  width: 280px;
  height: 280px;
  object-fit: cover;
}
.case-content1,
.content1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.case-content1 {
  position: absolute;
  height: 95.58%;
  width: 90.91%;
  top: 1.2%;
  right: 4.55%;
  bottom: 3.21%;
  left: 4.55%;
  gap: var(--gap-5xl);
}
.case-card1 {
  position: relative;
  width: 352px;
  height: 996px;
}
.control-the-ar {
  margin-block-start: 0;
  margin-block-end: 8px;
}
.with-your-arm {
  margin: 0;
}
.case-card-parent,
.featured-projects {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: var(--gap-13xl);
  font-size: var(--desktop-h-5-medium-size);
}
.featured-projects {
  width: 1120px;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-size: var(--desktop-h-2-medium-size);
  color: var(--shades-black);
  font-family: var(--desktop-h-4-semibold);
}
