.login1 {
  position: relative;
  letter-spacing: 0.02em;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 500;
}
.button3,
.login-signup1 {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.button3 {
  border-radius: var(--br-5xs);
  background-color: var(--shades-2);
  box-shadow: var(--button-dark-normal);
  height: 32px;
  overflow: hidden;
  padding: var(--padding-5xs) var(--padding-xs);
  box-sizing: border-box;
  justify-content: center;
  color: var(--shades-white);
}
.login-signup1 {
  position: relative;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  text-align: center;
  font-size: var(--desktop-p-3-aa-medium-size);
  color: var(--shades-4);
  font-family: var(--desktop-h-4-semibold);
}
