@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --desktop-h-4-semibold: Roboto;

  /* font sizes */
  --desktop-p-2-aa-medium-size: 14px;
  --desktop-h-4-semibold-size: 24px;
  --desktop-h-5-medium-size: 20px;
  --desktop-h-2-medium-size: 48px;
  --desktop-p-3-aa-medium-size: 12px;
  --desktop-p-1-medium-size: 16px;
  --desktop-h-1-semibold-size: 72px;

  /* Colors */
  --shades-2: #3e3b46;
  --shades-black: #211b2e;
  --white-25: rgba(255, 255, 255, 0.25);
  --white-60: rgba(255, 255, 255, 0.6);
  --shades-white: #fff;
  --solid-5: #8e53ff;
  --shades-3: #615b6f;
  --solid-7: #ff1d97;
  --shades-4: #858293;
  --color-black: #000;
  --color-whitesmoke: #f5f5f5;
  --color-slategray: #858193;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-13xl: 32px;
  --gap-5xl: 24px;
  --gap-base: 16px;
  --gap-xs: 12px;
  --gap-21xl: 40px;
  --gap-xl: 20px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-9xs: 4px;
  --padding-5xs: 8px;
  --padding-5xl: 24px;
  --padding-base: 16px;
  --padding-13xl: 32px;
  --padding-xs: 12px;
  --padding-3xs: 10px;

  /* border radiuses */
  --br-9xs: 4px;
  --br-5xs: 8px;
  --br-base: 16px;
  --br-xs: 12px;

  /* Effects */
  --translucency-m: 0px 8px 32px -16px rgba(135, 112, 195, 0.6);
  --button-dark-normal: 0px 2px 4px -1px #858293;
}
