.h11,
.h41 {
  align-self: stretch;
  position: relative;
}
.h11 {
  line-height: 80px;
  font-weight: 600;
  background: linear-gradient(
    225deg,
    #ffe800,
    #ffb444 17.21%,
    #ff5e86 46.37%,
    #e24eff 71.56%,
    #8e53ff
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.h41 {
  font-size: var(--desktop-h-4-semibold-size);
  line-height: 32px;
  font-weight: 500;
}
.hero-cta,
.hero-title1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.hero-title1 {
  align-self: stretch;
  align-items: flex-start;
  gap: var(--gap-base);
}
.hero-cta {
  width: 928px;
  align-items: center;
  gap: var(--gap-21xl);
}
.card-bg1 {
  position: absolute;
  margin: 0 !important;
  top: 352px;
  left: 0;
  border-radius: var(--br-base);
  background-color: var(--white-60);
  box-shadow: var(--translucency-m);
  backdrop-filter: blur(32px);
  border: 1px solid var(--white-25);
  box-sizing: border-box;
  width: 352px;
  height: 192px;
  overflow: hidden;
  flex-shrink: 0;
  z-index: 0;
}
.cover-icon {
  position: relative;
  width: 352px;
  height: 352px;
  object-fit: cover;
  z-index: 1;
}
.title2 {
  align-self: stretch;
  position: relative;
  line-height: 24px;
  font-weight: 500;
}
.create-wearable-avatars {
  margin-block-start: 0;
  margin-block-end: 8px;
}
.or-digital-garments {
  margin: 0;
}
.text2 {
  align-self: stretch;
  position: relative;
  font-size: var(--desktop-p-2-aa-medium-size);
  line-height: 24px;
  color: var(--shades-3);
}
.text-box1 {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
}
.label9 {
  position: relative;
  font-size: var(--desktop-p-3-aa-medium-size);
  letter-spacing: 0.02em;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--desktop-h-4-semibold);
  color: var(--shades-white);
  text-align: center;
}
.button6,
.card {
  border-radius: var(--br-5xs);
}
.button6 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-xs) var(--padding-base);
  background-color: var(--shades-2);
  box-shadow: var(--button-dark-normal);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.card {
  width: 352px;
  flex-direction: column;
  padding: var(--padding-5xl) var(--padding-base) var(--padding-13xl);
  box-sizing: border-box;
  justify-content: flex-start;
  gap: var(--gap-5xl);
  z-index: 2;
}
.body,
.body-parent,
.card,
.hero {
  display: flex;
  align-items: center;
}
.body {
  width: 352px;
  height: 552px;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
}
.body-parent,
.hero {
  flex-direction: row;
  justify-content: center;
  gap: var(--gap-13xl);
  font-size: var(--desktop-p-1-medium-size);
  color: var(--shades-black);
}
.hero {
  flex-direction: column;
  text-align: center;
  font-size: var(--desktop-h-1-semibold-size);
  color: var(--shades-3);
  font-family: var(--desktop-h-4-semibold);
}
